.App{
    background-color: $bg;
    font-family: 'Inter', sans-serif;
}

h1{
    color: $primary;
    font-weight: 700;
}

h2{
    color: $primary;
    font-weight: 700;
}

.Description{
    position: relative;
    width: 100vh;
    margin: 100px auto;
}


.Description-content{
    display: flex;
    width: 500px;
    margin-left: 40%;
    flex-direction: column;
    align-self: flex-start;    

h1{
        margin-bottom: 10px;
        font-size: 2.5em;
        padding-left: 55px;
    }

    p{
        margin-bottom: 32px;
        font-size: 1.5em;
        padding-left: 55px;

    }

    img {
        position: absolute;
        left: 13%;
        height: 300px;
        z-index: 2;
    }
}


.Tecnologias{
    h3{
        font-weight: 700;
        margin-bottom: 8px;
    }
    ul{
        color: $primary;
    }
    li{
        margin-bottom: 8px;
    }
    background-color: $secondary;
    padding: 10px 50px;
}

.Profile-retangle{
    position: absolute;
    left: 10%;
    margin-top: 45px;
    height: 270px;
    width: 250px;
    border: 3px solid $primary;
    z-index: 1;
}


